<template>
  <div class="edit-profile-page">
    <!--  1      -->
    <LogoHeader />

    <!--  ,  ,    -->
    <div class="menu">
      <div class="menu-item" :class="{ 'active-menu-item': activeMenu === '/mypage/reservations' }" @click="navigateTo('/mypage/reservations')"> </div>
      <div class="menu-item" :class="{ 'active-menu-item': activeMenu === '/mypage/membership' }" @click="navigateTo('/mypage/membership')"> </div>
      <div class="menu-item" :class="{ 'active-menu-item': activeMenu === '/mypage/edit-profile' }" @click="navigateTo('/mypage/edit-profile')"> </div>
    </div>

    <!--      -->
    <div class="profile-container">
      <div class="profile-header">
        <div class="profile-picture">
          <img :src="membershipImage(userMembership)" alt="  " />
        </div>
        <div>
          <span class="header-name"><strong>{{ userName }}</strong> </span>
        </div>
        <div class="balance-info">
          <span class="header-balance"> : <strong>{{ balance }}</strong></span>
        </div>
        <button class="recharge-button" @click="openRechargeWindow"></button>
      </div>

      <div class="profile-form">
        <div class="form-row"> 
          <div class="form-group">
            <label for="userName"></label>
            <input type="text" id="userName" v-model="userName" />
          </div>
          <div class="form-group">
            <label for="userEmail"></label>
            <input type="text" id="userEmail" v-model="userEmail" />
          </div>
        </div>

        <div class="form-row">
          <div class="form-group">
            <label for="userPassword"></label>
            <input type="text" id="userPassword" v-model="userPassword" />
          </div>
          <div class="form-group">
            <label for="confirmPassword"> </label>
            <input type="text" id="confirmPassword" v-model="confirmPassword" />
          </div>
        </div>
        <div class="form-row" id="userPhone">
          <label for="userPhone"></label>
          <div class="form-group">
            <select id="userPhone1" v-model="userPhonePart1">
              <option value="010">010</option>
              <option value="011">011</option>
              <option value="016">016</option>
              <option value="017">017</option>
            </select>
          </div><strong>-</strong>
          <div class="form-group">
            <input type="number" id="userPhone2" v-model="userPhonePart2" />
          </div><strong>-</strong>
          <div class="form-group">
            <input type="number" id="userPhone3" v-model="userPhonePart3" />
          </div>
        </div>
        <div class="form-row" id="address">
          <label for="address"></label>
          <div class="form-group">
            <input type="text" id="address" v-model="userAddress" @click="findAddress" readonly/>
          </div>
        </div>
        <button class="edit-profile-button">
          <i class="fas fa-pen"></i>  
        </button>
      </div>
    </div>
  </div>
</template>

<script>
import LogoHeader from '../common/LogoHeader.vue';

export default {
  name: 'EditProfile',
  components: {
    LogoHeader
  },
  props:{
    balance: {
      type: Number,
      required: true
    },
  },
  data() {
    return {
      activeMenu: this.$route.path, //   
      userName: '',
      userEmail: 'hong@example.com',
      userPassword: '',
      confirmPassword: '',
      userPhonePart1: '010',
      userPhonePart2: '',
      userPhonePart3: '',
      userAddress: '',
      userMembership: '',
      passwordVisible: false,
      userId: 0,
    };
  },
  watch: {
    //    activeMenu 
    $route(to) {
      this.activeMenu = to.path;
    }
  },
  methods: {
    navigateTo(route) {
      this.$router.push(route);
      this.activeMenu = route; //      
    },
    updateProfile() {
      //     (: API )
      if (this.userPassword !== this.confirmPassword) {
        alert('    .');
        return;
      }
      const userPhone = `${this.userPhonePart1}-${this.userPhonePart2}-${this.userPhonePart3}`;
      console.log(':', userPhone);
      alert(' .');
    },
    findAddress() {
      new window.daum.Postcode({
        oncomplete: (data) => {
          //       
          this.userAddress = data.address;
        }
      }).open();
    },
    openRechargeWindow() {
      const width = 450;
      const height = 215;
      const left = (window.screen.width / 2) - (width / 2); //   
      const top = (window.screen.height / 2) - (height / 2);
      window.open(`/myPage/recharge`, '', 
      `width=${width},height=${height},,top=${top},left=${left},
      toolbar=no,menubar=no,scrollbars=no,resizable=no,fullscreen=no`);
    },
    withdrawMembership() {
      //     (:    API )
      if (confirm('   ?')) {
        alert('  .');
      }
    },
    setInfo(response){
      this.userName = response.user.userName;
      this.userEmail = response.user.userEmail;
      this.userAddress = response.user.userAddress;
      this.$emit("getBalance", response.balance);
      this.userPhonePart1 = response.user.userPhone.split('-')[0];
      this.userPhonePart2 = response.user.userPhone.split('-')[1];
      this.userPhonePart3 = response.user.userPhone.split('-')[2];
      this.userMembership = response.user.userMembership.split('-')[1];
      this.userId = response.user.userId;
    },
    getUserInfo(){
      this.$axios.get(`/myPage/info`, {
        params: {
          email: sessionStorage.getItem('email')
        }
      }).then((response) => {
        this.setInfo(response.data);
      }).catch((error) => {
        console.error('     :', error);
      });
    },
    membershipImage(userMembership){
      return  require(`@/assets/img/membershipImg/${userMembership}.png`);
    }
  },
  mounted() {
    //   API  
    const script = document.createElement('script');
    script.src = 'https://t1.daumcdn.net/mapjsapi/bundle/postcode/prod/postcode.v2.js';
    document.head.appendChild(script);
    this.getUserInfo();
  }
}
</script>

<style scoped>
.edit-profile-page {
  padding-top: var(--header-height);
  padding-bottom: var(--footer-height);
  text-align: center;
}

/*  */
.menu {
  display: flex;
  justify-content: center;
  gap: 50px;
  padding-bottom: 20px;
}

.menu-item {
  width: 160px;
  padding: 10px 0;
  position: relative;
  cursor: pointer;
  font-weight: bold;
  color: #565656;
  white-space: nowrap;
}

.menu-item::after {
  content: "";
  position: absolute;
  bottom: -5px; /*       */
  left: 0;
  width: 100%;  /*   div   */
  height: 3px;
  background-color: black;
  transform: scaleX(0); /*    */
  transition: transform 0.3s ease;
}

.active-menu-item {
  color: black;  /*     black  */
}

.active-menu-item::after {
  transform: scaleX(1);  /*     */
}

.menu-item:hover::after {
  transform: scaleX(1);  /*     */
}

/*   */
.profile-container {
  border: 2px solid#60a191;
  border-radius: 10px;
  max-width: 800px;
  margin: 0 auto;
  padding: 20px;
  box-sizing: border-box;
  margin-bottom: 50px;
  background-color: #eaf1f02b;
}
.profile-header {
  display: flex;
  align-items: center;
  gap: 20px;
  padding-bottom: 10px;
  margin-bottom: 20px;
  font-size: 25px;
  border-bottom: 1px solid #60a191;
}
.profile-picture {
  display: flex;
  align-items: center;
}
.profile-picture img {
  width: 50px;
  height: 50px;
  border-radius: 50%;
  object-fit: cover;
}

.balance-info{
  margin-left: auto;
  text-align: center;
  justify-content: center;
}
.header-balance {
  font-size: 20px;
  margin-bottom: 10px; /*      */
}
.recharge-button{
  padding: 5px 18px;
  background-color: #fac100;
}
.recharge-button:hover{
  background-color: #d5a400;
}

.recharge-button,
.edit-profile-button {
  color: #fff;
  border: none;
  border-radius: 5px;
  cursor: pointer;
}
.edit-profile-button{
  margin-left: auto;
  padding: 10px 20px;
  width: 18%;
  background-color: #60a191;
}
.edit-profile-button:hover{
  background-color: #4f8578;

}
.profile-form {
  display: flex;
  flex-direction: column;
  gap: 20px;
}
.form-row {
  align-items: center;
  display: flex;
  gap: 20px;
}
.form-group {
  flex: 1;
  display: flex;
  flex-direction: column;
}
label {
  text-align: left;
  justify-content: center;
  margin-bottom: 5px;
  font-weight: bold
}

label[for="address"],
label[for="userPhone"]{
  width: 10%;
}

input {
  padding: 10px;
  border: 1px solid #000;
  border-radius: 5px;
}
#userPhone1{
  padding: 10px;
  border: 1px solid #000;
  border-radius: 5px;
}
input[type="number"]::-webkit-outer-spin-button,
input[type="number"]::-webkit-inner-spin-button {
    -webkit-appearance: none;
    margin: 0;
}
#address, #userPhone{
  margin-top: 10px;
}
</style>